  display: flex;
  flex-direction: column;
}
@media (min-width: 960.99px) {
  .tab-content__column:hover .tab-content__text {
    opacity: 1;
    scale: 1;
  }
}
.tab-content__image {
  cursor: pointer;
  width: 100%;
  overflow: hidden;
  border-radius: 30px;
