  .tab-content__text {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    background: rgba(60, 59, 81, 0.8078431373);
    scale: 0;
    opacity: 0;
    transition: opacity 0.4s, scale 0.4s ease 0s;
  }
  .tab-content__text span {
    height: 100%;
    display: flex;
    align-items: center;
  }
}
@media screen and (max-width: 960px) {
  .tab-content__text {
    display: block;
    position: relative;
    color: #000;
  }
}
.tab-content__button {
  right: 0;
  bottom: 0;
  background: #2b3769;
  border: 1px solid transparent;
}
.tab-content__button span {
  color: #fff;
}
.tab-content__button:hover .icon {
  transform: rotate(-45deg);
  background: #2b3769;
}
.tab-content__button:hover ._icon::before {
  color: #fff;
  font-weight: 400;
}
.tab-content__button:hover span {
  color: #000;
}
.tab-content__button:hover::before {
  background: #fff;
